.element {
    display: flex;
    justify-content: space-between;
}
.price {
    display: flex;
    align-items: center;
}
.ingredient {
    display: flex;
    align-items: center;
}
.element_image {
    width: 64px;
    height: 64px;
    box-sizing: border-box;
    border: solid 2px #801AB2;
    border-radius: 100px;
    display: flex;
    justify-content: center;
}