.feed_section {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}
.title {
    text-align: left;
}
.list {
    display: flex;
    flex-direction: column;
    height: 100vh;
    gap: 16px;
    overflow: hidden auto;
}
.stats {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 100px 0 0 56px;
    gap: 60px;
    width: 584px;
    height: 626px;
}
.stats_board {
    display: flex;
    flex-direction: row;

    padding: 0px;
    gap: 40px;
    justify-content: flex-start;
    width: 584px;
    height: 206px;
}
.stats_title {
    list-style-type: none;
}
.complited_all {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 0px;
    width: 584px;
    height: 150px;
}
.complited_today {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 0px;
    width: 584px;
    height: 150px;
}